<template>
  <div class="rhymetonyms">
    <p>
      <router-link v-bind:to="{ name: 'Rhymetonyms' }">Rhymetonyms</router-link>
      &bull;
      <router-link v-bind:to="{ name: 'Rhymesaurus' }">Rhymesaurus</router-link>
    </p>
    <form v-on:submit.prevent="findWords">
      <p>Find rhymes for 
        <input type="text" v-model="rhyme"> which are antonyms of 
        <input type="text" v-model="antonym"> 
        <button type="submit">Search</button></p>
    </form>

    <ul v-if="results && results.length>0" class="results">
      <li v-for="(item, index) in results" :key="index" class="item">
        <p><strong>{{ item.word }}</strong></p>
        <p>{{ item.score }}</p>
      </li>
    </ul>

    <div v-else-if="results && results.length === 0" class="no-results">
      <h2>No Words Found</h2>
      <p>Please adjust your search to find more words.</p>
    </div>

    <ul v-if="errors.length > 0" class="errors">
      <li v-for="(error, index) in errors" :key="index">
        {{ error.message }}
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Rhymetonyms',
  data () {
    return {
      results: null,
      errors: [],
      antonym: '',
      rhyme: ''
    }
  },
  methods: {
    findWords: function() {
      this.results = null;
      let queryID = `${this.rhyme}-${this.antonym}`;
      let cacheExpiry = 24 * 60 * 60 * 1000; // 24 hr cache expiry
      console.log('query ID: ' + queryID);
      let cachedQuery = this.$ls.get(queryID);
      if (cachedQuery) {
        console.log('Previous query cache found.');
        this.results = cachedQuery;
      } else {
        console.log('No cache found for: ' + queryID);
        this.fetchAPIResults();
      }
    },
    fetchAPIResults: function() {
      this.results = null;
      let queryID = `${this.rhyme}-${this.antonym}`;
      let cacheExpiry = 24 * 60 * 60 * 1000; // 24 hr cache expiry
      axios.get('https://api.datamuse.com/words', {
        params: {
          rel_rhy: this.rhyme,
          rel_ant: this.antonym,
        }
      })
      .then( response => {
        this.$ls.set(queryID, response.data, cacheExpiry);
        console.log('Cache created for: ' + queryID);
        this.results = response.data;
      })
      .catch( error => {
        this.errors.push(error);
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rhymetonyms {
  font-size: 1.4rem;
}

input[type="text"]{
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #333;
  width: 300px;
  font-size: 1.4rem;
  color: #2c3e50;
  font-weight: 300;
  background: rgba(0,0,0,0.02);
  padding: 0.5rem;
}
button{
  background: #333;
  padding: 0.5rem;
  font-weight: 300;
  color: #fff;
  border: none;
  cursor: pointer;
  font-size: 1.4rem;
}
h1, h2 {
  font-weight: normal;
}

ul.results {
  list-style-type: none;
  padding: 0;
}

.results li {
  display: inline-block;
  margin: 10px;
  border: solid 1px #333;
  padding: 0.5rem;
  width: 200px;
  min-height: 100px;
  color: #fff;
  background: rgba(0,0,0,0.7);
}
ul.errors {
  list-style-type: none;
}
.errors li {
  border: 1px solid red;
  color: red;
  padding: 0.5rem;
  margin: 10px 0;
}

a {
  color: #42b983;
}
</style>



